export default {
    props: ['modalOptions'],
    computed: {
        modal() {
            let modal = this.modalOptions;
            modal = {
                title: modal.title || '提示',
                text: modal.text,
            };
            return modal;
        },
    },
    data() {
        return {
            show: false, // 是否显示模态框
            resolve: '',
            reject: '',
            promise: '', // 保存promise对象
        };
    },
    methods: {
        submit() {
            this.resolve('submit');
        },
        close(type) {
            this.show = false;
            this.reject(type);
        },
        confirm() {
            this.show = true;
            this.promise = new Promise((resolve, reject) => {
                this.resolve = resolve;
                this.reject = reject;
            });
            return this.promise; //返回promise对象,给父级组件调用
        },
    }
}



