import Item from '../components/GuestbookItem.vue'
import PageNav from '../components/PageNav.vue'
import CommentBox from '../components/CommentBox.vue'
import Spinner from '../components/Spinner.vue'
import InfoTip from '../components/InfoTip.vue'

export default {

  components: {
    Item,
    PageNav,
    CommentBox,
    Spinner,
    InfoTip
  },
  data() {
    return {
      loading: false,
      curPage: 1
    }
  },
  watch: {
    '$route': function () {
      this.fetchData(this.$store)
    }
  },
  beforeMount() {
    this.fetchData(this.$store)
  },
  methods: {
    fetchData(store) {
      let page = store.state.route.query.page;
      this.loading = true;
      store.dispatch('LOAD_GUESTBOOK_LIST', {
        page: page
      }).then(() => {
        this.loading = false;
        this.curPage = page
      });
    },
    changePage(page) {
      this.$router.push({
        name: 'guestbooks',
        query: {
          page: page
        }
      })
    }
  },
  computed: {
    guestbooks() {
      return this.$store.state.items
    },
    totalCount() {
      return this.$store.state.total_count
    }
  },
  preFetch: function (store) {
    return this.methods.fetchData(store);
  },
  created() {
    this.$store.dispatch('LOAD_MENU_ID', 2)
  }
}




